import React from "react";
import ReactDOM from "react-dom";
import { Catalog, pageLoader } from "catalog";

const pages = [
	{
		path: "/",
		title: "Bienvenido",
		theme: {
			logoSrc: 'logo.png',
			pageHeadingBackground: '#FF0000'
		},
		content: pageLoader(() => import("./pages/es/start.md"))
	},
	{
		title: 'Plugins',
		pages: [
			{
				path: '/es/plugins/webcheckout',
				title: 'WebCheckout',
				content: pageLoader(() => import("./pages/es/plugins/webcheckout.md"))
			},
			{
				path: '/es/plugins/payment_orders',
				title: 'Ordenes de pago',
				content: pageLoader(() => import("./pages/es/plugins/payment_orders.md"))
			},
			{
				path: '/es/plugins/payment_codes',
				title: 'Códigos de Pago',
				content: pageLoader(() => import("./pages/es/plugins/payment_codes.md"))
			},
			// Other subpages of 'Basics'
		]
	},
	{
		title: 'Transacciones',
		pages: [
			{
				path: '/es/transactions/endpoints',
				title: 'Consultas',
				content: pageLoader(() => import("./pages/es/transactions/endpoints.md"))
			},
			// Other subpages of 'Basics'
		]
	},
	{
		title: 'Librerias',
		pages: [
			{
				path: '/es/libraries/javascript',
				title: 'Javascript',
				content: pageLoader(() => import("./pages/es/libraries/javascript.md"))
			},
			{
				path: '/es/libraries/ecommerce',
				title: 'Ecommerce',
				content: pageLoader(() => import("./pages/es/libraries/ecommerce.md"))
			},
			// Other subpages of 'Basics'
		]
	},
	{
		title: 'Otros',
		pages: [
			{
				path: '/es/testcards',
				title: 'Tarjetas de Prueba',
				content: pageLoader(() => import("./pages/es/testcards.md"))
			},
			{
				path: '/es/homologation',
				title: 'Homologación',
				content: pageLoader(() => import("./pages/es/homologacion.md"))
			},
			{
				path: '/es/statuses',
				title: 'Códigos de Estado',
				content: pageLoader(() => import("./pages/es/statuses.md"))
			},
			{
				path: '/es/webhooks',
				title: 'Notificaciones',
				content: pageLoader(() => import("./pages/es/webhooks.md"))
			},
		]
	},
];

ReactDOM.render(
	<Catalog
		title="Mobbex {DEV}"
		pages={pages} 
	/>,
	document.getElementById("catalog")
);



